.hide {
  display: none !important; }

.show {
  display: block !important; }

.cf::after {
  clear: both;
  content: "";
  display: table; }

.fl {
  float: left; }

.fr {
  float: right; }

.upper {
  text-transform: uppercase; }

.capitalize {
  text-transform: capitalize; }

.ellipsis {
  display: inline-block;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal; }

.text-center {
  text-align: center !important; }

.text-left {
  text-align: left !important; }

.text-right {
  text-align: right !important; }

.full-height {
  height: 100%; }

.two-column {
  padding-bottom: 9999px !important;
  margin-bottom: -9999px !important;
  overflow: hidden; }

.test {
  background: resolve("app-store.png") at-2x; }

.test2 {
  background: resolve("sprites/add-client.png") no-repeat at-2x; }

.test3 {
  background: resolve("sprites/book.png") at-2x; }
